import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
//import IconButton from "@material-ui/core/IconButton";
//import MenuIcon from "@material-ui/icons/Menu";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles((theme) => ({
  appbar: {
    backgroundColor: "white",
    borderBottom: "2px solid black",
    //"#00b885"
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  buttons: {
    marginLeft: "auto",
  },
  button: {
    color: "green",
    border: "none",
    "&:hover": {
      border: "1px solid white",
      backgroundColor: "#ff5e5e",
      color: "white",
    },
    borderRadius: 8,
    marginLeft: 6,
  },
}));

export default function DenseAppBar(props) {
  const classes = useStyles();
  return (
    <React.Fragment>
      <AppBar position="fixed" className={classes.appbar}>
        <Toolbar variant="dense">
          <Typography variant="h6">
            <img src={require("../../../assets/icons/virus.png")}></img>
          </Typography>
          <div className={classes.buttons}>
            <Button onClick={props.clickParaiba} className={classes.button}>
              Paraíba
            </Button>
            <Button onClick={props.clickBrazil} className={classes.button}>
              Brasil
            </Button>
            <Button onClick={props.clickWorld} className={classes.button}>
              Mundo
            </Button>
          </div>
        </Toolbar>
      </AppBar>
    </React.Fragment>
  );
}
