var width = window.screen.availWidth;

export const Image = (props) => {
  return <img src={props.src} width={width} height={width * 0.45} />;
};

export const Begin = () => {
  return (
    <Carousel autoplay>
      <a href="https://coronavirus.es.gov.br/" target="_blank">
        <div>
          <Image src={imgs.img1.url} />
        </div>
      </a>
      <a href="https://covid.saude.gov.br/" target="_blank">
        <div>
          <Image src={imgs.img2.url} />
        </div>
      </a>
    </Carousel>
  );
};

